<template>
  <Layout>
    <div class="box">
      <router-link
        class="labels"
        :to="`/Labels/Edit/${tag.id}`"
        v-for="tag in labelList"
        :key="tag.id"
      >
        <span>{{ tag.name }}</span>
        <Icons name="right" />
      </router-link>
      <button class="btn" @click="create">新建标签</button>
      <el-dialog
        title="请输入新增标签"
        :visible.sync="dialogVisible"
        width="80%"
      >
        <input type="text" v-model="newlabel" />
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="confirm">确 定</el-button>
        </span>
      </el-dialog>
    </div>
  </Layout>
</template>

<script>
export default {
  data() {
    return {
      // labelList: [],
      dialogVisible: false,
      newlabel: "",
    };
  },
  created() {
    this.$store.commit("getTagList");
  },
  computed: {
    labelList() {
      return this.$store.state.tagList;
    },
    ll() {
      this.labelList = [];
      return [1];
    },
  },
  methods: {
    create() {
      this.dialogVisible = true;
    },
    confirm() {
      const value = this.newlabel;
      if (value === "" || value === " ") {
        alert("标签不能为空");
        return;
      }
      this.$store.commit("createTag", this.newlabel);
      this.newlabel = "";
      this.dialogVisible = false;
    },
  },
  watch: {
    labelList() {
      if (this.labelList) {
        if (this.labelList.length >= 11) {
          const button = document.querySelector(".btn");
          button.classList.add("addLocation");
        }
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.box{
  height: 80vh;
  overflow: auto;
}
.labels {
  display: flex;
  min-height: 7vh;
  justify-content: space-between;
  border-bottom: 1px solid rgb(189, 188, 188);
  align-items: center;
  padding-right: 16px;
  margin-left: 16px;
}
.btn {
  margin-top: 16px;
  padding: 8px 16px;
  border-radius: 4px;
  border-color: rgb(239, 191, 147);
  cursor: pointer;
  background: rgb(239, 191, 147);
}
.addLocation {
  position: fixed;
  top: 78%;
  left: 50%;
  transform: translateX(-50%);
}
</style>