<template>
  <div>
    <label class="notes">
      <span class="name">{{ nameValue }}</span>
      <input :value="value" type="text" :placeholder="placeholder" @input="inputblur" />
    </label>
  </div>
</template>

<script>
export default {
  props: ["nameValue", "placeholder", "value"],
  data() {
    return {};
  },
  methods: {
    inputblur(e) {
      this.$emit('update:value',e.target.value)
    },
  },
};
</script>

<style lang="scss" scoped>
.notes {
  display: flex;
  /* display: block; */
  padding: 0 16px;
  align-items: center;
  .name {
    padding-right: 16px;
  }
  input {
    height: 64px;
    flex-grow: 1;
    border: none;
    background: transparent;
  }
}
</style>