<template>
  <div>
    <div id="view"></div>
  </div>
</template>

<script>
import * as echarts from "echarts/core";
import { PieChart } from "echarts/charts";
import {
  TitleComponent,
  TooltipComponent,
  GridComponent,
  DatasetComponent,
  TransformComponent,
} from "echarts/components";
import { LabelLayout, UniversalTransition } from "echarts/features";
import { CanvasRenderer } from "echarts/renderers";
echarts.use([
  PieChart,
  TitleComponent,
  TooltipComponent,
  GridComponent,
  DatasetComponent,
  TransformComponent,
  LabelLayout,
  UniversalTransition,
  CanvasRenderer,
]);
export default {
  props: ["value"],
  watch: {
    value() {
      let myChart = echarts.init(document.querySelector("#view"));
      let price = []
      if(!this.value || this.value.length === 0){
        price = [
          {value:200,name:'没有数据,示例1'},
          {value:100,name:'没有数据,示例2'},
          {value:300,name:'没有数据,示例3'},
          {value:400,name:'没有数据,示例4'}
        ]
      }
      this.value.forEach(item=>{
        price.push({value:Number(item.numberValue),name:item.tags[0]})
      })
      let option = {
       series : [
        {
            name: '访问来源',
            type: 'pie',    // 设置图表类型为饼图
            radius: '55%',  // 饼图的半径，外半径为可视区尺寸（容器高宽中较小一项）的 55% 长度。
            data:price      // 数据数组，name 为数据项名称，value 为数据项值
                
        }
    ]
      };
      myChart.setOption(option);
    },
  },
};
</script>

<style lang="scss" scoped>
#view {
  width: 100vw;
  height: 50vh;
  margin-top: 20px;
}
</style>