<template>
  <div>
    <Layout :name="'layout'">
      <Numberpad @update:value="onUpdateNumber" />
      <Types :value.sync="record.type" />
      <Notes
        class="inputback"
        nameValue="备注"
        placeholder="请输入备注"
        :value.sync="record.notes"
      />
      <Tags
        :tagsvalue.sync="record.tags"
      />
    </Layout>
  </div>
</template>

<script>
import Layout from "@/components/Layout.vue";
import Numberpad from "@/components/Numberpad.vue";
import Types from "@/components/Types.vue";
import Notes from "@/components/Notes.vue";
import Tags from "@/components/Tags.vue";
export default {
  components: { Layout, Numberpad, Types, Notes, Tags },
  props: ["tagsvalue"],
  data() {
    return {
      record: {
        tags: [], //tags组件返回的值
        notes: "", //notes组件返回的值
        type: "-", //types 组件返回的值
        numberValue: "0", //numberpad 组件返回的值
        createDate: "", //数据新建时间
      },
      recordItem: [],
    };
  },
  methods: {
    onUpdateNumber(value) {
      this.record.numberValue = value;
      this.record.createDate = new Date();
      this.$store.commit('createRecord',this.record)
      this.record = {
        tags: [],
        notes: "",
        type: "-",
        numberValue: "0",
        createDate: "",
      };
    }, //点OK时，触发
  },
};
</script>

<style lang="scss" scoped>
.inputback {
  background: rgb(222, 174, 126);
}
</style>