<template>
  <Layout>
    <div class="editTitle">
      <router-link to="/Labels">
      <Icons class="icons" name="left" />
      </router-link>
      <span>编辑标签</span>
      <span></span>
    </div>

    <Notes class="inputback" nameValue="标签名" placeholder="请输入标签名" :value.sync="inputValue" />
      <button class="btn" @click="delectButton">删除标签</button>
  </Layout>
</template>

<script>
import Notes from "@/components/Notes.vue";
export default {
  components: { Notes },
  data(){
    return{
      inputValue:'',
      inputValueid:''
    }
  },
  created(){
    this.$store.commit('getTagList')

    let list = this.$store.state.tagList
    let id = this.$route.params.id.toString()
    list.forEach(item=>{
      if(item.id === id ){
        this.inputValue = item.name
        this.inputValueid = item.id
      }
    })
  },
  watch:{
    inputValue(){
      console.log(222)
      let obj = {id:this.inputValueid,value:this.inputValue}
      this.$store.commit('updateTag',obj)
    }
  },
  methods:{
    delectButton(){
      this.$store.commit('delectTag',this.inputValueid)
    }
  }
};
</script>

<style lang="scss" scoped>
.editTitle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 12px;
  font-size: 20px;
  background: rgb(239, 191, 147);
  .icons{
    width: 25px;
    height: 25px;
  }
}
.inputback{
  background: rgb(239, 191, 147);
  margin-top: 14px;
}
.btn {
  margin-top: 16px;
  padding: 8px 16px;
  border-radius: 4px;
  border-color: rgb(239, 191, 147);
  cursor: pointer;
  background: rgb(239, 191, 147);
}
</style>