<template>
  <div>
    <Layout class="static">
      <Types :value.sync="type" />
      <TimeSwitch class="views" :type="type" :value.sync="tiemselect" />
      <SwitchView :value.sync="expendList" />
      <div>
        <h3>{{ day }}</h3>
        <ul class="showData">
          <li v-for="(tag,index) in expendList" :key="index">
            项目：{{ tag.tags[0] }},花费：￥{{ tag.numberValue }},备注:{{
              tag.notes
            }}
          </li>
        </ul>
      </div>
    </Layout>
  </div>
</template>

<script>
import Types from "@/components/Types.vue";
import TimeSwitch from "@/components/TimeSwitch.vue";
import SwitchView from "@/components/SwitchView.vue";
import dayjs from "dayjs";
export default {
  data() {
    return {
      type: "-",
      tiemselect: "threeday",
      expendList: [],
      incomeList: [],
      day: "近三天",
    };
  },
  components: { Types, TimeSwitch, SwitchView },
  beforeCreate() {
    this.$store.commit("getRecord");
  },
  computed: {
    recordList() {
      return this.$store.state.recordItem;
    },
  },
  mounted() {
    this.computelist();
  },
  watch: {
    tiemselect(newvalue, old) {
      this.computelist();
      this.day =
        newvalue === "threeday"
          ? "近三天"
          : newvalue === "week"
          ? "一个星期"
          : newvalue === "month"
          ? "一个月"
          : "所有";
    },
    type(newvalue) {
      this.computelist();
    },
  },
  methods: {
    computelist() {
      let list = this.recordList.filter((item) => item.type === this.type);
      let now;
      if (this.tiemselect === "threeday") {
        now = dayjs().subtract(2, "day");
      } else if (this.tiemselect === "week") {
        now = dayjs().subtract(7, "day");
      } else if (this.tiemselect === "month") {
        now = dayjs().subtract(30, "day");
      } else {
        this.expendList = list;
        return;
      }
      this.expendList = [];
      list.forEach((item) => {
        let day = dayjs(item.createDate);
        if (day.isAfter(now, day)) {
          this.expendList.push(item);
        }
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.static {
  display: flex;
  flex-direction: column;
  .views {
    flex-grow: 1;
  }
  h3 {
    text-align: left;
    margin-left: 16px;
  }
  .showData {
    overflow: auto;
    height: 15vh;
    width: 100vw;
  }
}
</style>