<template>
  <svg class="icon">
    <use :xlink:href="'#'+name" />
  </svg>
</template>

<script>
let importAll = (requireContext) => requireContext.keys().forEach(requireContext);
try{
    importAll(require.context('../assets/icons',true,/\.svg$/)) //require.context 指定要去哪个目录
}catch(error){
    console.log(error)
}
export default {
  name:'Icons',
  props:['name']
}
</script>

<style lang="scss" scoped>
.icon{
  width: 1em;
  height: 1em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}
</style>