<template>
  <div>
    <ul class="types">
      <li :class="{selected:value === '-'}" @click="select('-')">支出</li>
      <li :class="{selected:value === '+'}" @click="select('+')">收入</li>
    </ul>
  </div>
</template>

<script>
export default {
  props:['value'],
  methods:{
    select(value){
      this.$emit('update:value',value)
    }
  }
};
</script>

<style lang="scss" scoped>
.types {
  display: flex;
  background: rgb(239, 191, 147);
  font-size: 24px;
  width: 100vw;
  li {
    width: 50%;
    min-height: 64px;
    text-align: center;
    line-height: 64px;
    position: relative;
    &.selected::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 4px;
      background: #333;
    }
  }
}
</style>