import Vue from 'vue'
import Vuex, { Store } from 'vuex'
import getId from '@/lib/getId'
import router from '../router'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    tagList: [],
    recordItem: [],
    delectTagmessage:'',
    createMessage:true
  },
  mutations: {
    createTag(state, value) {
      const id = getId().toString()
      const names = state.tagList.map(item =>item.name)
      state.createMessage = true
      if(names.indexOf(value) >=0){
        state.createMessage = false
        alert('标签名重复了')
        return
      }
      state.tagList.push({id,name:value})
      window.localStorage.setItem('tagList',JSON.stringify(state.tagList))
    },
    getTagList(state) {
      state.tagList = JSON.parse(window.localStorage.getItem('tagList')) || []
      if(!state.tagList || state.tagList.length === 0){
        store.commit('createTag','买衣服')
        store.commit('createTag','吃')
        store.commit('createTag','住')
        store.commit('createTag','地铁')
      }
    },
    delectTag(state,id) {
      state.tagList.forEach((item,index)=>{
        if(item.id === id){
          state.tagList.splice(index,1)
          store.commit('saveTag')
          router.back()
        }
      })
    },
    updateTag(state,obj){
      state.tagList.forEach(item=>{
        if(item.id === obj.id){
          item.name = obj.value
        }
      })
      store.commit('saveTag')
    },
    saveTag(state){
      window.localStorage.setItem('tagList',JSON.stringify(state.tagList))
    },
    createRecord(state, value) {
      if(value.tags.length === 0){
        return alert('没有选择标签')
      }
      state.recordItem.push(value)
      window.localStorage.setItem('recordItem', JSON.stringify(state.recordItem))
    },
    getRecord(state) {
      state.recordItem = JSON.parse(window.localStorage.getItem('recordItem')) || []
    }
  },
  actions: {
  },
  modules: {
  }
})

export default store
