<template>
  <div class="numberPad">
    <div class="output">{{moenySum}}</div>
    <div class="buttons">
      <button @click="add('1')">1</button>
      <button @click="add('2')">2</button>
      <button @click="add('3')">3</button>
      <button @click="delect">删除</button>
      <button @click="add('4')">4</button>
      <button @click="add('5')">5</button>
      <button @click="add('6')">6</button>
      <button @click="moenySum = '0'">清空</button>
      <button @click="add('7')">7</button>
      <button @click="add('8')">8</button>
      <button @click="add('9')">9</button>
      <button @click="ok" class="isOk">OK</button>
      <button @click="add('0')" class="zero">0</button>
      <button>.</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "Numberpad",
  data(){
    return{
      moenySum:'0'
    }
  },
  props:['value'],
  methods:{
    add(value){
      if(this.moenySum === '0'){
        this.moenySum = value
        return
      }
      this.moenySum = this.moenySum + value
    },
    delect(){
      const leng = this.moenySum.length
      if(leng === 1){
        this.moenySum = '0'
        return
      }
      this.moenySum = this.moenySum.slice(0,-1)
    },
    ok(){
      this.$emit('update:value',this.moenySum)
      this.moenySum = '0'
    }
  }
};
</script>

<style lang="scss" scoped>
@import "~@/assets/style/helper.scss";
.numberPad {
  .output {
    text-align: right;
    padding-right: 10px;
    min-height: 64px;
    line-height: 64px;
    font-size: 36px;
    font-family: Consolas, monospace;
    box-shadow: inset 0 -5px 5px -5px rgba(0, 0, 0, 0.5),
      inset 0 5px 5px -5px rgba(0, 0, 0, 0.5);
  }
  .buttons {
    @extend %clearfix;
    button {
      width: 25%;
      height: 7.9vh;
      border: none;
      float: left;
      background: transparent;
      &.zero {
        width: 50%;
      }
      &.isOk {
        height: calc(7.9 * 2vh);
        float: right;
      }
    }
    $numberbgc: rgb(244, 224, 211);
    button:nth-child(1) {
      background: $numberbgc;
    }
    button:nth-child(2),
    button:nth-child(5) {
      background: darken($numberbgc, 4%);
    }
    button:nth-child(3),
    button:nth-child(6),
    button:nth-child(9) {
      background: darken($numberbgc, 4 * 2%);
    }
    button:nth-child(4),
    button:nth-child(7),
    button:nth-child(10),
    button:nth-child(13) {
      background: darken($numberbgc, 4 * 3%);
    }
    button:nth-child(11),
    button:nth-child(8) {
      background: darken($numberbgc, 4 * 4%);
    }
    button:nth-child(12),
    button:nth-child(14) {
      background: darken($numberbgc, 4 * 5%);
    }
  }
}
</style>