<template>
  <div class="tags">
    <div class="new">
      <button @click="dialogVisible = true">新增标签</button>
    </div>
    <ul class="current">
      <li
        :class="{ selected: tagsvalue.indexOf(tag.name) >= 0 }"
        v-for="tag in tags"
        :key="tag.id"
        @click="select(tag.name)"
      >
        {{ tag.name }}
      </li>
    </ul>
    <el-dialog title="请输入新增标签" :visible.sync="dialogVisible" width="80%">
      <input type="text" v-model="newtagslabel" />
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="confirm">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      selectTag: [],
      dialogVisible: false,
      newtagslabel: "",
    };
  },
  props: ["tagsvalue"],
  beforeCreate() {
    this.$store.commit("getTagList");
  },
  computed: {
    tags() {
      return this.$store.state.tagList;
    },
  },
  methods: {
    select(value) {
      let index = this.tagsvalue.indexOf(value);
      if (index >= 0) {
        this.tagsvalue.splice(index, 1);
        this.$emit("update:tagsvalue", this.tagsvalue);
        return;
      }
      this.tagsvalue.push(value);
      this.$emit("update:tagsvalue", this.tagsvalue);
    },
    confirm() {
      if (this.newtagslabel === "" || this.newtagslabel === " ") {
        this.$message({
          message: '标签名不能为空',
          type: 'warning'
        });
        return;
      }
      this.$store.commit("createTag", this.newtagslabel);
      this.newtagslabel=''
      this.dialogVisible = false;
      if (this.$store.state.createMessage) {
        this.$message({
          message: '创建成功',
          type: 'success'
        });
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.tags {
  display: flex;
  flex-grow: 1;
  flex-direction: column-reverse;
  padding: 16px;
  overflow: auto;
  .new {
    width: fit-content;
    button {
      margin-top: 16px;
      border: none;
      background: none;
      padding: 0 3px;
      color: #5e5c5c;
      border-bottom: 1px solid #5e5c5c;
      cursor: pointer;
    }
  }
  .current {
    display: flex;
    flex-wrap: wrap;
    li {
      background: rgb(239, 191, 147);
      margin-top: 5px;
      padding: 0 16px;
      margin-right: 5px;
      height: 24px;
      border-radius: 12px;
      line-height: 24px;
      &.selected {
        background: #e95e3b;
      }
    }
  }
}
</style>