<template>
  <div>
    <ul class="time">
      <li
        :class="{ select: value === 'threeday' }"
        @click="timeDate('threeday')"
      >
        近三天
      </li>
      <li :class="{ select: value === 'week' }" @click="timeDate('week')">
        一个星期
      </li>
      <li
        :class="{ select: value === 'month' }"
        @click="timeDate('month')"
      >
        一个月
      </li>
      <li :class="{ select: value === 'all' }" @click="timeDate('all')">
        {{ type === "-" ? "支出" : "收入" }}全部
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      selectvalue: "threeday",
    };
  },
  props: ["type",'value'],
  methods: {
    timeDate(value) {
      this.$emit('update:value',value)
    },
  },
};
</script>

<style lang="scss" scoped>
.time {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: #d09678;
  li {
    height: 50px;
    line-height: 50px;
    width: 25%;
    position: relative;
    &.select::after {
      position: absolute;
      content: "";
      left: 0;
      bottom: 0;
      height: 3px;
      width: 100%;
      background: black;
    }
  }
}
</style>